interface Question {
  id: string;
  question: string;
  type: 'multiple-choice' | 'scale' | 'text' | 'yes-no' | 'checkbox' | 'age-input';
  options?: Array<string | {value: string; label: string; genders?: string[]}>;
  required: boolean;
  label?: string;
  isGenderQuestion?: boolean;
  visibleForGenders?: string[];
}

interface QuestionnaireData {
  [key: string]: Question[];
}

interface QuestionOption {
  value: string;
  label: string;
  genders?: string[];
}

interface AgeInputProps {
  id: string;
  label: string;
  value: string;
  onChange: (value: string) => void;
}

const AgeInput: React.FC<AgeInputProps> = ({ id, label, value, onChange }) => {
  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const nextValue = event.target.value;
    if (/^\d*$/.test(nextValue)) {
      onChange(nextValue);
    }
  };

  return (
    <div className="lonvia-age-input">
      <div className="input-container">
        <input
          type="text"
          id={id}
          value={value}
          onChange={handleChange}
          required
          placeholder=" "
          inputMode="numeric"
          className="age-field w-full p-4 border border-gray-300 rounded-lg focus:ring-2 focus:ring-[#10552E] focus:border-transparent"
        />
        <label htmlFor={id} className="label text-gray-600">
          {label}
        </label>
        <div className="underline" />
      </div>
    </div>
  );
};

